import React, { PropTypes }  from 'react';
import UEditor from 'simple-react-ui';
import {Form, Button, Icon,Input,Col} from 'antd';
import { ueditorconfig, ueditorall} from '../../utils/config';

const FormItem = Form.Item;

class EditLawyerProfile extends React.Component {
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        let params = {
          lawyerDesc: values.lawyerDesc,
        }
        this.props.onSubmit(params)
      }
    });
  }
  ueAfterInit = (ue) => {
    const {getFieldsValue, setFieldsValue, validateFields} = this.props.form;
    ue.addListener('contentChange', function(){
      const fieldsValue = getFieldsValue();
      const newFieldsValue = {...fieldsValue, 'lawyerDesc': ue.getContent()};
      setFieldsValue(newFieldsValue);
      validateFields();
    });
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    const {lawyerDesc,id} = this.props;
    const formItemLayout = {
      labelCol: { span: 1 },
      wrapperCol: { span: 10 },
    };
    return (
      <Form onSubmit={this.handleSubmit}>

        <FormItem style={{display:'none'}}
                  {...formItemLayout}
                  label="标题"
        >
          {getFieldDecorator('id', {
            initialValue:id,
          })(<Input/>
          )}
        </FormItem>

        <FormItem
          {...formItemLayout}
          label="内容"
        >
          {getFieldDecorator('lawyerDesc', {
            initialValue: lawyerDesc||'',
            rules: [
              { required: true, message: '内容不能为空' },
            ],
          })(<Input type="hidden"/>
          )}

          <UEditor style={{width:'100%'}} id="content" name="content"  height={500} initialContent={lawyerDesc}
                   afterInit={this.ueAfterInit}
                   uconfigSrc={ueditorconfig}
                   ueditorSrc={ueditorall} />
        </FormItem>

        <FormItem wrapperCol={{ span: 12, offset: 6 }}>
          <Col span={6} style={{ textAlign: 'right' }}>
            <Button type="primary" htmlType="submit">保存</Button>
          </Col>
        </FormItem>
      </Form>
    );
  }
}
export default Form.create()(EditLawyerProfile);



