import React, {PropTypes} from 'react'
import {connect} from 'dva'
import {Row, Col, Card} from 'antd'
import Profile from '../components/profile/Profile';
import LawyerProfile from '../components/profile/lawyerProfile'
import styles from './dashboard.less'
import {color} from '../utils'


function Dashboard ({dashboard, dispatch}) {
  const {profile,profileEdit,LawyerprofileEdit} = dashboard;

  const editProfile = {
    ...profile,
    profileEdit:profileEdit,
    editProfileClick(){
      dispatch({
        type:'dashboard/editProfileModeChange'
      })
    },
    onSubmit:(values) => {
      dispatch({
        type: 'dashboard/update',
        payload: values,
      })
    }
  }
  const editLawyerProfile = {
    ...profile,
    LawyerprofileEdit:LawyerprofileEdit,
    editLawyerProfileClick(){
      dispatch({
        type:'dashboard/editLawyerProfileModeChange'
      })
    },
    onSubmit:(values) => {
      dispatch({
        type: 'dashboard/updateLawyer',
        payload: values,
      })
    }
  }
  return (
    <Row gutter={24}>
      <Col span={12}>
        <Card>
            <Profile {...editProfile}/>
        </Card>
      </Col>
      <Col span={12}>
        <Card></Card>
      </Col>
      <Col span={24}>
        <Card>
          <LawyerProfile {...editLawyerProfile}/>
        </Card>
      </Col>
    </Row>
  )
}

Dashboard.propTypes = {
}

export default connect(({dashboard}) => ({dashboard}))(Dashboard)



