import React,{Component} from 'react';
import {Row, Col,Form, Input,Button,Tag} from 'antd';
import styles from './Profile.less';
import EditProfile from './editProfile'
const FormItem = Form.Item;

class Profile extends Component {
  constructor(props){
    super(props);
  }

  componentDidMount = ()=>{

  }




  render(){
    const {firmId,firm, picUrl, realName, address, certificate, mobile, email, majors,onSubmit,profileEdit} = this.props;
    const profileProps = {
      firmId,
      firm,
      picUrl,
      realName,
      address,
      certificate,
      mobile,
      email,
      majors,
      onSubmit
    }

    return (
      <div className={styles.normal}>
        <Button onClick={this.props.editProfileClick.bind(this)}>{profileEdit ? '取消修改' : '修改资料'}</Button>
        {profileEdit ?
          <EditProfile {...profileProps} ></EditProfile>
        :
          <div>
            <Row className={styles.row}>
              <Col span={6}>姓    名:</Col>
              <Col span={18}>{realName}</Col>
            </Row>
            <Row className={styles.row}>
              <Col span={6}>律师照片:</Col>
              <Col span={18}>
                <img src={picUrl}style={{maxHeight:40, maxWidth:80}}/>
              </Col>
            </Row>
            <Row className={styles.row}>
              <Col span={6}>执业机构:</Col>
              <Col span={18}>{firm}</Col>
            </Row>
            <Row className={styles.row}>
              <Col span={6}>专业领域:</Col>
              <Col span={18}>
                 {majors &&majors.map((item,index)=>{
                  return (
                    <Tag key={index}>{item.values}</Tag>
                  )
                })}
              </Col>
            </Row>
            <Row className={styles.row}>
              <Col span={6}>执业证号:</Col>
              <Col span={18}>{certificate}</Col>
            </Row>
            <Row className={styles.row}>
              <Col span={6}>联系地址:</Col>
              <Col span={18}>{address}</Col>
            </Row>

          </div>
        }
      </div>
    );
  }
}

export default Profile;



